<template>
  <v-col xs="12" sm="12" md="4" lg="3" xl="3">
    <v-hover>
      <template v-slot:default="{ hover }">
        <v-card class="mx-auto" max-width="344">
          <v-row v-show="hover">
            <v-col>
              <edit-project v-on:getVal="getVal" :projectData="dataModel" />
              <delete-project v-on:getVal="getVal" :projectData="dataModel" />
            </v-col>
          </v-row>
          <v-img
            :lazy-src="mainUrl + dataModel.image_url"
            :src="mainUrl + dataModel.image_url"
            height="200px"
          ></v-img>
         <v-hover open-delay="1000" v-slot="{ hover }">
            <v-row>
              <v-col>
                <v-chip
                  id="chip"
                  outlined
                  v-if="dataProject.vedio_url"
                  v-text="'click to show video'"
                  :class="hover ? 'secondary' : 'primary'"
                >
                </v-chip>
                <v-card-text
                  id="video"
                  v-if="hover"
                  v-html="dataProject.vedio_url"
                ></v-card-text>
              </v-col>
            </v-row>
          </v-hover>
          <v-card-title v-text="dataModel.title[lang]"> </v-card-title>
          <v-card-subtitle>
            <br />
            <p>Total Amount : &ensp; {{ dataModel.total_amount }} $</p>
            <p>current Amount : &ensp; {{ dataModel.current_amount }} $</p>
          </v-card-subtitle>
          <!-- <v-card-subtitle
            v-text="dataModel.total_amount + '$'"
          ></v-card-subtitle> -->
          <v-col cols="12">
            <v-row>
              <v-col v-if="section" cols="6">
                section :
                <br />
                <v-chip v-if="section" v-text="section.name[lang]" class="secondary">
                  Default
                </v-chip>
              </v-col>

              <v-col v-if="campaign" cols="6">
                campaign :
                <br />
                <v-chip v-text="campaign.title[lang]" class="primary">
                  Default
                </v-chip>
              </v-col>
            </v-row>
          </v-col>
         
          <v-card-subtitle v-if="areas">
            areas :
            <v-chip-group active-class="primary--text" column>
              <v-chip v-for="area in areas" :key="area.id">
                {{ area.name[lang] }}
              </v-chip>
            </v-chip-group>
          </v-card-subtitle>
          <v-card-actions>
            <v-btn color="orange lighten-2" text> Description </v-btn>
            <v-spacer></v-spacer>
            <v-btn icon @click="show = !show">
              <v-icon>{{
                show ? "mdi-chevron-up" : "mdi-chevron-down"
              }}</v-icon>
            </v-btn>
          </v-card-actions>
          <v-expand-transition>
            <div v-show="show">
              <v-divider></v-divider>
              <v-card-text v-text="dataModel.description[lang]"> </v-card-text>
            </div>
          </v-expand-transition>
        </v-card>
      </template>
    </v-hover>
  </v-col>
</template>

<script>
export default {
  props: ["dataProject"],
  data() {
    return {
      // show to show or hed description in card
      show: false,
      dataModel: this.dataProject,
      mainUrl: this.$store.state.imageUlr + "project/",
      areas: this.dataProject.areas,
      section: this.dataProject.section,
      campaign: this.dataProject.campaign,
    };
  },
  components: {
    "edit-project": () => import("../../components/projects/edit.vue"),
    "delete-project": () => import("../../components/projects/delete.vue"),
  },
  methods: {
    getVal() {
      this.$emit("getVal");
    },
  },
  computed: {
    lang() {
      return this.$store.getters.lang;
    },
  },
};
</script>

<style>
 
#video {
  position: absolute;
  top: 0;
  z-index: 1023;
}
</style>